body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-color: #212121;
}
.app-root-container {
    font-family: 'Poiret One', sans-serif;
}
.landing-splash-container {
    min-width: 600px;
    margin-top: 64px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
    height: calc(100vh - 64px);
}

.intro-image {
    margin-left: 10vw;
    -webkit-align-self: center;
            align-self: center;
}

.intro-text {
    color: white;
    white-space: nowrap;
    min-width: 200px;
    -webkit-align-self: center;
            align-self: center;
}

@media only screen and (min-width: 500px) {
    .landing-splash-container {
        background-position: 10px 10px;
    }

    .intro-section {
        top: 300px;
        left: 600px;
    }
}
.header-container {
    height: 64px;
    width: 100vw;
    position: fixed;
    background-color: #212121;
    border-bottom: 1px solid rgba(255, 255, 255, 0.158);
    top: 0;
    z-index: 2;
    grid-template-columns: auto 200px 32px;
    grid-template-areas: ". nav .";
}
.nav-flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    width: 100%;
}

.nav-item {
    -webkit-justify-content: center;
            justify-content: center;
    color: white;
    font-family: 'Poiret One', cursive;
    font-size: 14px;
    margin-top: 42px;
    margin-right: 16px;
    margin-left: 16px;
    cursor: pointer;
}
